import React from 'react';
import '../css/layout.css';
import {Container,Card,Button,Row,Col} from 'react-bootstrap';
import Traffic from '../Assets/traffic.png';
function Home() {
    return (
        <Container fluid className="contact-content" style={{width:"100%",margin:"5%",padding:"0"}}>
                <Row className='text-center' style={{width:"100%"}} >
                    
                      
                            <Col style={{width:"100%"}}>
                             <Card bg='dark' className='WorkCard'>
                                <Container className='ImageBox'>
                                    <img alt= 'TrafficClipart' src ={Traffic}/>
                                </Container>
                                <Card.Body>
                                    <Card.Title> <h1>Accident Project</h1></Card.Title>
                                    <Card.Text className='text-center'>
                                        <b1> In Spring 2022 during my Data Structures Class for a college project my group worked together to create a web app. The goal of this webapp was to pull traffic accident information about an area based on lattitude and longitude to help users find out the worst locations to avoid when driving near a location. This was accomplished using C++ and building a heap object and then using heap sort to quickly find a certain number of accidents based on severity as determined by the dataset.
                                        </b1>
                                    </Card.Text>
                                </Card.Body>
                                <Card.Link className='text-center'>
                                    <Row>
                                    <Col>
                                    <Button href="http://45.61.185.184:8080/AccidentProject/" variant="outline-secondary"><b1>Test Site</b1></Button>
                                    </Col>
                                    <Col>
                                    <Button href="https://github.com/daa729/AccidentProject" variant="outline-secondary"><b1>Repository</b1></Button>
                                    </Col>
                                    </Row>
                                </Card.Link>
                            </Card>
                        </Col>
               
                </Row>

            </Container>
    );
}

export default Home;