import React from 'react';
import '../css/layout.css';
import {Container,Card, Row, Col} from 'react-bootstrap';
import GELogo from '../Assets/GatorEsportsLogo.png';
import DMLogo from'../Assets/DMlogo.png';
function Home() {
    return (
        <Container fluid className="contact-content" style={{width:"100%",margin:"5%",padding:"0"}}>
            <Row>
                <Col>
                <Card bg='dark' className='WorkCard'>
                  
                        <Card.Header>
                        <Container className='ImageBox'>
                            <img alt="Gator Esports"src={DMLogo} style={{width:"100%"}}/>
                        </Container>
                        </Card.Header>
                       <Card.Title> <h1>Asistant Director</h1></Card.Title>
                        <Card.Body style={{"text-align": "center"}}>
                            <b1>I currently serve as the Assistant Director of Dance Marathon at UF's Design and Tech team. The goal of this role is to help create a team enviroment and encourage fundraising among the team through planning of team bonding and fundraising ideas. This money is raised in support of Children's Miracle Network Hospitals. 

                            </b1>
                       </Card.Body>
                </Card>
                </Col>
                  <Col>
                <Card bg='dark' className='WorkCard'>
                  
                        <Card.Header>
                        <Container className='ImageBox'>
                            <img alt="Gator Esports"src={GELogo} style={{width:"100%"}}/>
                        </Container>
                        </Card.Header>
                       <Card.Title> <h1>Vice President</h1></Card.Title>
                        <Card.Body style={{"text-align": "center"}}>
                            <b1>I currently serve as the Vice President of Gator Esports. Gator Esports is an offical Uf organiation dedicated to the support and management of its semi-professional video game players. Our organization aims to keep our players interests first as they comepte against other colleges and universities.</b1>
                       </Card.Body>
                </Card>
                </Col>
            </Row>
        
       </Container> 
    )
}

export default Home;