import React from 'react';
import { Navbar, Nav, Container,} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {NavLink, Outlet} from "react-router-dom"
import '../css/layout.css';
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

import { SocialIcon } from 'react-social-icons';

function Hello(){
    return (
        
        
            <Navbar style={{height:"10%",marginBottom:"2%",marginTop:"2%"}}> 
                
                    
                    
                <div className='container'>
                    <Nav className="me-auto">
                        <navs>
                         
                            <NavLink exact className="navs" to="/"><t1>DAA</t1></NavLink>
                            <NavLink  exact className="navs"  to="/Work"><t1>Work</t1>  </NavLink>
                            <NavLink exact className= "navs"  to="/Projects">  <t1>Projects</t1> </NavLink>
                            <NavLink exact className= "navs"  to="/Involvment">  <t1>Involment</t1> </NavLink>
                            
                        </navs>
                    </Nav>
                    <Nav className="">
                            <SocialIcon className='socialIcon' bgColor='rgba(255, 255, 255, 0)'fgColor='currentColor'url="https://www.linkedin.com/in/dandersonallen/" />
                            <SocialIcon className='socialIcon' bgColor='rgba(255, 255, 255, 0)'fgColor='currentColor' url="https://www.facebook.com/profile.php?id=100052139963753" />
                            <SocialIcon className='socialIcon' bgColor='rgba(255, 255, 255, 0)'fgColor='currentColor' url="https://www.instagram.com/daajr/" />
                    </Nav>
                   
                </div>
            </Navbar>
            
        
    );
};
function Background(){
    const particlesInit = async (main) => {
        console.log(main);

        // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
        // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
        // starting from v2 you can add only the features you need reducing the bundle size
        await loadFull(main);
    };
    return(
        <div className="App">


            <Particles
                id="tsparticles"
                init={particlesInit}

                options={{
                    "fullScreen": { "enable": true, "zIndex": 0 },
                    "particles": {
                        
                        "number": {
                            "value": 18,
                            "density": {
                                "enable": true,
                                "value_area": 800
                            }
                        },
                        "color": {
                            "value": "#6EFF62",
                            animation: {
                                enable: false,
                                speed: 50,
                                sync: false
                            }
                        },
                        "shape": {
                            "type": "point",
                            "options": {
                                "sides": 6
                            }
                        },
                        "opacity": {
                            "value": 0.8,
                            "random": false,
                            "anim": {
                                "enable": true,
                                "speed": .3,
                                "opacity_min": 0.1,
                                "sync": false
                            }
                        },
                        "size": {
                            "value": 60,
                            "random": true,
                            "anim": {
                                "enable": true,
                                "speed": 5,
                                "size_min": 1,
                                "sync": false
                            }
                        },
                        "rotate": {
                            "value": 0,
                            "random": true,
                            "direction": "clockwise",
                            "animation": {
                                "enable": true,
                                "speed": 5,
                                "sync": false
                            }
                        },
                        "links": {
                            "blink": false,
                            "color":"random",
                            "consent": false,
                            "distance": 500,
                            "enable": true,
                            "frequency":.5,
                            "opacity": .3,
                            
                            "triangles": {
                                "enable": true
                            },
                            "width": 1,
                            "warp": false,
                             
                            
                        },
                        "move": {
                            "enable": true,
                            "speed": 2,
                            "direction": "none",
                            "random": false,
                            "straight": false,
                            "out_mode": "out",
                            "attract": {
                                "enable": false,
                                "rotateX": 600,
                                "rotateY": 1200
                            }
                        }
                    },
                    "interactivity": {
                        "events": {
                            "onhover": {
                                "enable": false,
                                "mode": ["grab"]
                            },
                            "onclick": {
                                "enable": false,
                                "mode": "bubble"
                            },
                            "resize": true
                        },
                        "modes": {
                            "grab": {
                                "distance": 400,
                                "line_linked": {
                                    "opacity": 1
                                }
                            },
                            "bubble": {
                                "distance": 400,
                                "size": 40,
                                "duration": 2,
                                "opacity": 8,
                                "speed": 3
                            },
                            "repulse": {
                                "distance": 200
                            },
                            "push": {
                                "particles_nb": 4
                            },
                            "remove": {
                                "particles_nb": 2
                            }
                        }
                    },
                    "retina_detect": true,
                    "background": {
                        "color": "#0F0F0F",
                        
                       
                        
                      
                    },
                
                }}
            />
        </div>
    )
}
function Layout (){
    return (
        
            <div>
            
            <Container fluid>
                <Background/>
                <div style={{marginBottom:"5%"}}>
                    <Hello />
                    <Outlet/>
                </div>
                
               
            </Container>
            
            </div>
        
     
        
        
    );
};
export default Layout;

