import React from 'react';
import '../css/layout.css';
import Rinker from "../Assets/RinkerLogo.png";
import Agritisan from "../Assets/AgritisanLogo.png"
import { Button,Container, Row, Col, Card } from 'react-bootstrap';
function Home() {
    return (
        <>

            <Container>
                <Row>
                    
                        <Col>
                            <Card bg='dark' className='WorkCard'>
                                <Container className='ImageBox'>
                                    <img alt= 'RinkerLogo' src ={Rinker} style={{width:"100%"}}/>
                                </Container>
                                
                                <Card.Body>
                                    <Card.Title> <h1>Student VR Technician</h1></Card.Title>
                                    <Card.Text className='text-center'>
                                        <b1> I currently work for the Rinker School of Construction Managment at the Univeristy of Florida. My job is to create virtual enviroments in the Unity Application for use in 3D VR simulation stuies. These studies currently focus on the reactions of to drone and other stimuli on the construction site.
                                        </b1>
                                    </Card.Text>
                                </Card.Body>
                                 <Card.Link className='text-center'>
                                <Button href="https://caslab.wixsite.com/caslab" variant="outline-secondary"><b1>Rinker School</b1></Button>
                                </Card.Link>
                            </Card>
                            </Col>
                            <Col>
                             <Card bg='dark' className='WorkCard'>
                                <Container className='ImageBox'>
                                    <img alt= 'AgritisanLogo' src ={Agritisan} style={{height:"100%",width:"100%",borderRadius:"180px"}}/>
                                </Container>
                                <Card.Body>
                                    <Card.Title> <h1>Website Design and App Developer</h1></Card.Title>
                                    <Card.Text className='text-center'>
                                        <b1> I worked over the summer in Singapore for a startup company called Agritisan which was housed inside the BOP Hub. The goal of this startup was originally to sell produce from their urban farms, during my time there this transitioned to education about urban farming and sustainability. My job was to help with this transtion with website redesign and brand identity as well as help them to develop an education app using Flutter.
                                        </b1>
                                    </Card.Text>
                                </Card.Body>
                                <Card.Link className='text-center'>
                                    <Button href="https://www.agritisan.sg/" variant="outline-secondary"><b1>Agritisan</b1></Button>
                                    
                                </Card.Link>
                            </Card>
                        </Col>
               
                </Row>

            </Container>
        </>
    );
}

export default Home;